*,
*::before,
*::after {
  box-sizing: border-box;
}

ul,
li {
  padding: 0;
  list-style: none;
}

body,
h1,
h2,
h3,
h4,
p,
ul,
li {
  margin: 0;
}
button {
  cursor: pointer;
  background: transparent;
  border: 1px solid transparent;
}
a {
  text-decoration: none;
}

img {
  display: block;
  width: auto;
  max-width: 100%;
}
